// fry 848ddc6ae49df2782f8eff2737c1fffb00f14c6c

export const setup = async function () {
    let { nrow, resizable } = this.fryargs;
    
      this.hgutters = (r) => {
        const ch = this.fryelement.children.item(r);
        if (ch && ch.frycomponents && ch.frycomponents.length > 0) {
            const comp = ch.frycomponents[0];
            if (comp.isGutter) {
                return [comp];
            }
        }
        return [];
      }

      this.vgutters = (c) => {
        // c必定为0
        const gutters = [];
        for (let r=0; r<nrow; r++) {
            const ch = this.fryelement.children.item(r);
            if (ch && ch.frycomponents && ch.frycomponents.length > 0) {
                const comp = ch.frycomponents[0];
                if (comp.isGutter) {
                    gutters.push(comp);
                }
            }
        }
        return gutters;
      }
    
    this.fryembeds = [];
};
