// fry 848ddc6ae49df2782f8eff2737c1fffb00f14c6c

export const setup = async function () {
    let { ncol, resizable } = this.fryargs;
    
      this.hgutters = (r) => {
        // r必定为0
        const gutters = [];
        for (let c=0; c<ncol; c++) {
            const ch = this.fryelement.children.item(c);
            if (ch && ch.frycomponents && ch.frycomponents.length > 0) {
                const comp = ch.frycomponents[0];
                if (comp.isGutter) {
                    gutters.push(comp);
                }
            }
        }
        return gutters;
      }

      this.vgutters = (c) => {
        const ch = this.fryelement.children.item(c);
        if (ch && ch.frycomponents && ch.frycomponents.length > 0) {
            const comp = ch.frycomponents[0];
            if (comp.isGutter) {
                return [comp];
            }
        }
        return [];
      }
    
    this.fryembeds = [];
};
