import "./styles.css";
import { setup as fryui_button_Button } from "./fryui/button@Button";
import { setup as fryui_grid_Grid } from "./fryui/grid@Grid";
import { setup as fryui_grid_Gutter } from "./fryui/grid@Gutter";
import { setup as fryui_grid_HCenter } from "./fryui/grid@HCenter";
import { setup as fryui_grid_HSplit } from "./fryui/grid@HSplit";
import { setup as fryui_grid_VCenter } from "./fryui/grid@VCenter";
import { setup as fryui_grid_VSplit } from "./fryui/grid@VSplit";
let setups = { fryui_button_Button, fryui_grid_Grid, fryui_grid_Gutter, fryui_grid_HCenter, fryui_grid_HSplit, fryui_grid_VCenter, fryui_grid_VSplit };
import { hydrate as hydrate_with_setups } from "fryweb";
export const hydrate = async (rootElement) => await hydrate_with_setups(rootElement, setups);